body {
  font-family: 'Rockwell', sans-serif;
  font-size: 14px; }

.section {
  position: relative;
  min-height: calc(100vh - 80px - 56px);
  background: #eaf6ec;
  padding: 80px 0 40px; }

.container {
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px; }
  @media (max-width: 992px) {
    .container {
      max-width: 960px; } }
  @media (max-width: 768px) {
    .container {
      max-width: 720px; } }
  @media (max-width: 576px) {
    .container {
      max-width: 540px; } }
  @media (max-width: 480px) {
    .container {
      max-width: 95%; } }

.main-title {
  position: relative;
  text-align: center;
  color: #059681;
  font-size: 26px;
  margin: 0 auto 50px; }
  @media (max-width: 480px) {
    .main-title {
      font-size: 22px; } }
  .main-title:before, .main-title:after {
    content: ' ';
    display: inline-block;
    vertical-align: middle;
    top: 50%;
    transform: translateY(-50%);
    background: #c7d5d3;
    width: 50px;
    height: 1px; }
    @media (max-width: 480px) {
      .main-title:before, .main-title:after {
        width: 40px; } }
  .main-title:before {
    margin-right: 10px; }
  .main-title:after {
    margin-left: 10px; }

.btn {
  color: #fff;
  background-color: #28a745;
  border-color: #28a745;
  transition: 0.3s; }
  .btn:hover {
    background: #2cbc4e; }

.home-btn {
  position: absolute;
  right: 40px;
  bottom: 40px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 20px;
  color: #fff;
  background-color: #3DC7F4;
  box-shadow: -1px 2px 8px 0px rgba(29, 50, 33, 0.75); }
  @media (max-width: 480px) {
    .home-btn {
      width: 40px;
      height: 40px;
      right: 20px;
      bottom: 20px; } }

.header {
  position: relative;
  padding: 40px 0;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05);
  z-index: 10; }
  .header .logo {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    max-width: 140px;
    max-height: 137px; }
    .header .logo__link {
      width: 100%; }

.footer {
  background: #3a3e3b;
  padding: 20px 0;
  color: #9ca9a0; }
  @media (max-width: 576px) {
    .footer-inner {
      flex-direction: column;
      text-align: center; } }
  @media (max-width: 576px) {
    .footer .policy {
      margin-top: 8px; } }
  .footer .policy__link {
    color: #9ca9a0; }
    .footer .policy__link:hover {
      text-decoration: underline; }

.features {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 90px 0; }
  .features__item {
    width: 30%;
    text-align: center; }
    @media (max-width: 576px) {
      .features__item {
        width: 100%;
        margin: 16px 0; } }
    .features__item:hover .features__icon {
      box-shadow: 0 0 0 55px #fff inset, 0 0 0 5px #76c08a; }
    .features__item:hover .icon {
      color: #76c08a; }
    .features__item:hover .features__title:before {
      width: 80px; }
  .features__link {
    cursor: pointer; }
  .features__icon {
    position: relative;
    margin: 0 auto;
    width: 110px;
    height: 110px;
    border-radius: 50%;
    background-color: #76c08a;
    box-shadow: 0 0 0 2px #fff inset, 0 0 0 5px #76c08a;
    transition: all 0.3s; }
    @media (max-width: 667px) {
      .features__icon {
        width: 80px;
        height: 80px; } }
  .features .icon {
    color: #fff;
    font-size: 50px;
    transition: all 0.3s ease-out; }
    @media (max-width: 667px) {
      .features .icon {
        font-size: 30px; } }
  .features__title {
    position: relative;
    font-size: 24px;
    color: #76c08a;
    padding-bottom: 10px;
    margin: 20px 0 10px; }
    .features__title:before {
      content: ' ';
      position: absolute;
      background: #76c08a;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 24px;
      height: 2px;
      transition: all 0.3s; }
    @media (max-width: 667px) {
      .features__title {
        font-size: 20px; } }
  .features__subtitle {
    font-size: 13px;
    line-height: 16px;
    color: #5c695e; }

.upload__inner {
  display: flex;
  justify-content: center;
  margin-bottom: 30px; }

.upload__file {
  cursor: pointer;
  position: relative;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  background-color: #76c08a;
  box-shadow: 0 0 0 2px #fff inset, 0 0 0 5px #76c08a;
  margin: 0 20px;
  transition: all 0.3s; }
  .upload__file:hover {
    box-shadow: 0 0 0 55px #fff inset, 0 0 0 5px #76c08a; }
  .upload__file:hover .icon {
    color: #76c08a; }
  @media (max-width: 667px) {
    .upload__file {
      width: 80px;
      height: 80px; } }

.upload input[type="file"] {
  display: none; }

.upload label {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer; }

.upload .icon {
  color: #fff;
  font-size: 50px;
  transition: all 0.3s ease-out; }
  @media (max-width: 667px) {
    .upload .icon {
      font-size: 30px; } }

.upload .btn-send {
  display: block;
  width: 120px;
  margin: 0 auto;
  font-size: 15px;
  padding: 14px 6px;
  border-radius: 20px; }
  @media (max-width: 667px) {
    .upload .btn-send {
      width: 100px;
      padding: 9px 6px; } }

.search__inner {
  position: relative;
  width: 90%;
  margin: 0 auto;
  border: solid 1px #c1ddbd;
  border-radius: 6px; }
  @media (max-width: 414px) {
    .search__inner {
      width: 100%; } }

.search__input {
  display: inline-block;
  width: 100%;
  padding: 8px 70px 8px 8px; }
  .search__input::placeholder {
    color: #a9a9a9; }

.search__btn {
  position: absolute;
  top: -1px;
  right: 0;
  padding: 6px 18px 7px;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px; }

.articles__name {
  display: block;
  font-size: 20px;
  line-height: 26px;
  padding: 10px 6px;
  color: #000;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.09);
  transition: 0.3s; }
  .articles__name:hover {
    color: #76c08a; }
